.b-portf {
  position: relative;
  background: white; }
  .b-portf__wrap {
    display: flex;
    flex-wrap: wrap; }

.el-portf {
  flex: 0 0 50%;
  height: 50vh;
  background: center/cover no-repeat;
  position: relative;
  padding-bottom: 156px;
  display: flex;
  align-items: flex-end; }
  .el-portf:hover:after {
    background: rgba(0, 0, 0, 0.4); }
  .el-portf:after {
    position: absolute;
    content: "";
    background: rgba(0, 0, 0, 0.5);
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition-duration: 0.3s; }
  .el-portf__body {
    max-width: 350px;
    width: 100%;
    margin: 0 auto;
    font-size: 20px;
    line-height: 110%;
    color: white;
    font-family: "Circe";
    position: relative;
    z-index: 1; }
